  }
}

@media (max-width: 768px) {
  .u-not--sp {
    display: none !important;
  }
}

@media (max-width: 999px) {
  .u-inner--tablet {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media (max-width: 768px) {
  .u-inner--sp {
    margin-left: 10px;
    margin-right: 10px;
  }
}

